import * as React from "react";
import styled from "styled-components";

import { Cell } from "./Cell";

const Root = styled.a`
    padding: 20px 15px;
    margin-bottom: 15px;
    background-color: #333333;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
`;

const LogoTitle = styled.h1`
    margin-left: 5px;
    margin-bottom: 10px;
    font-size: 2rem;
    font-weight: bold;
    line-height: 2rem;
    letter-spacing: 2px;
`;

const LogoSubtitle = styled.div`
    margin-left: 5px;
    line-height: 1rem;
`;

export default class Header extends React.PureComponent {
    render() {
        return (
            <Cell>
                <LogoTitle>
                    Aleksey Fedotov
                </LogoTitle>

                <LogoSubtitle>
                    /* home page of one programmer */
                </LogoSubtitle>
            </Cell>
        )
    }
}
